
.section-first {
  height: 2000px;
  background-color: white;
  position: relative;
  // -webkit-box-shadow: inset 0px 10px 16px -8px rgba(0,0,0,0.75);
  // -moz-box-shadow: inset 0px 10px 16px -8px rgba(0,0,0,0.75);
  // box-shadow: inset 0px 10px 16px -8px rgba(0,0,0,0.75);
  &::before {
    position: absolute;
    content: "";
    display: inline-block;
    height: 1vh;
    width: 100vw;
    background-color: white;
    top: -2vh;
  }
}
